/* src/css/register.css */

.register-page {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100vh;
	background: linear-gradient(45deg, #FF80A9, #FF4C8B); /* Fun gradient */
	font-family: 'Comic Sans MS', cursive, sans-serif; /* Playful font */
}

.register-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-grow: 1;
	padding: 2rem;
}

.register-card {
	background: #ffffff;
	padding: 2rem;
	border-radius: 12px;
	box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
	max-width: 500px;
	width: 100%;
}

.register-heading {
	text-align: center;
	margin-bottom: 1rem;
	font-size: 2rem; /* Reduced font size */
	color: #FF4C8B; /* Matching the gradient theme */
	font-weight: bold;
}

.register-input-group {
	display: flex;
	flex-direction: column;
	gap: 1.5rem;
}

.register-input {
	padding: 1rem;
	margin-top: .5rem;
	border: 2px solid #FF80A9; /* Soft pink border */
	border-radius: 8px;
	font-size: 1rem; /* Reduced font size */
	transition: 0.3s;
	width: 100%; /* Full width for inputs */
	color: #333333; /* Darker text color */
}

.register-input::placeholder {
	color: #777777; /* Darker placeholder color */
}

.register-input:focus {
	border-color: #FF4C8B; /* Change border color on focus */
	outline: none; /* Remove default outline */
	box-shadow: 0 0 5px rgba(255, 76, 139, 0.6); /* Add a subtle shadow to highlight focus */
	border-width: 2px; /* Ensure the border width is consistent */
}

.register-button {
	padding: 1rem;
	margin-top: 1rem;
	background-color: #FF4C8B; /* Bright pink button */
	color: white;
	font-size: 1.2rem; /* Reduced font size */
	border: none;
	border-radius: 8px;
	cursor: pointer;
	transition: background-color 0.3s;
	width: 100%; /* Full width for button */
}

.register-button:hover {
	background-color: #FF80A9; /* Hover effect */
}

.error-message {
	color: red;
	text-align: center;
	margin-top: 1rem;
	font-size: 1rem;
}

.account-link {
	text-align: center;
	margin-top: 1rem;
	font-size: 1.1rem; /* Slightly reduced font size */
	color: #333333;
}

.login-link {
	color: #FF4C8B;
	font-weight: bold;
	text-decoration: none;
}

.login-link:hover {
	text-decoration: underline;
}

/* Additional style for first and last name input fields */
.input-group {
	display: flex;
	justify-content: space-between;
}

.input-group input {
	width: 48%; /* Ensure both inputs fit on the same line */
}
